import { Header } from "@/components/layout";
import { Sidebar } from "@/components/layout";

export default function MyLayout({ children, ...props }) {
    return (
        <html lang="ko">
            <body>
                <Header />
                <div className="flex">
                    <Sidebar />
                    <main>{children}</main>
                </div>
            </body>
        </html>
    );
}
